import { useMemo, useState } from "react";
import ItemBox from "./ItemBox";
import { useWishlist } from "../../contexts/wishlistContext";
import Pagination from "../../components/Pagination";

const Wishlist = () => {
  const { wishlistItems } = useWishlist();
  const [currentPage, setCurrentPage] = useState(1);
  const limit = 10;

  const displayedItems = useMemo(() => {
    const firstPageIndex = (currentPage - 1) * limit;
    const lastPageIndex = firstPageIndex + limit;
    return wishlistItems.slice(firstPageIndex, lastPageIndex);
  }, [currentPage, wishlistItems]);

  return (
    <main className="container max-w-full py-8 max-lg:py-4 space-y-4">
      <div className="flex items-center justify-center gap-4">
        <h1 className="font-semibold text-2xl text-center">Wishlist</h1>
        <p className="font-semibold text-2xl text-primeRed">
          ({wishlistItems.length})
        </p>
      </div>

      <section>
        {displayedItems.length === 0 ? (
          <h1 className="text-2xl text-center font-secondary font-medium">
            Wishlist is Empty
          </h1>
        ) : (
          <div className="flex flex-wrap items-center gap-4 max-lg:gap-2">
            {wishlistItems.map((item) => (
              <ItemBox key={item._id} product={item} />
            ))}
          </div>
        )}

        <div className="PAGINATION">
          <Pagination
            currentPage={currentPage}
            totalCount={wishlistItems.length}
            pageSize={limit}
            onPageChange={(page) => setCurrentPage(page)}
          />
        </div>
      </section>
    </main>
  );
};

export default Wishlist;
